module.exports = {"cover":"","date":"","id":1,"lead":"","sections":[{"sections":[{"paragraphs":[{"text":"\"Stop E40\" is a public campaign against the construction of the E40 waterway that can pass through the rivers of Poland, Belarus and Ukraine. The developers of this project assume that the E40 will become a new trade route between the ports of the Baltic and Black Seas, attract investments to the region and create new jobs. We, <a href='/en/articles/public-opinion'> the environmental coalition </a>, believe that the project is ill-conceived, economically inefficient, and that the construction of the E40 will be a disaster both for the unique region of the Belarusian Polesye, as and for a number of valuable natural territories of Poland and Ukraine."}],"title":""}],"title":"What is #Stop_E40?"},{"paragraphs":[{"text":"This is a project to establish a navigable route longer than 2,000 km that they want to construct through the Vistula, Pripyat and Dnieper rivers in order to connect the Baltic and Black Seas. The project proponents call it the \"reconstruction\" because the E40 trunk waterway (on the route Gdansk-Warsaw-Brest-Pinsk-Mozyr-Kiev-Kherson) <a href='http://docs.cntd.ru/document/901784389'> exists on paper since 1996</a>. Except that from Warsaw to Brest it is not navigable, therefore, plans for the \"reconstruction\" of the E40 suggest building a bypass canal in Poland, six or seven dams and locks in the Pripyat, as well as dredging on the entire route so that multitonnage \"river-sea\" vessels can follow the course of our rivers."},{"text":"The development of the project began at the end of 2013 and is continuing to the present day. The initiator of the E40 construction is the coalition of organizations from three countries (Belarus, Poland and Ukraine) headed by the Republican Unitary Operational and Construction Enterprise \"Dnepro-Bugsky Canal\". The feasibility study (the most important project document, which specifies what, where and why will be built) was developed by a consortium led by the Gdansk Maritime Institute. The work on the project was funded by the European Union within the Program for Transboundary Cooperation among three countries. Now the initiators of the project are in search of funding for further research and work."}],"title":"I have never heard of E40. What is it?"},{"paragraphs":[{"text":"The problem is that projects of this scale have both positive and negative effects - and the latter, of course, are poorly defined in the documentation of the developers. We wondered what consequences such a drastic change in the rivers would have for the entire region. To answer this question, we asked independent experts from different areas to evaluate the existing feasibility study and express their opinion on the possible outcomes of the E40 waterway construction."}],"title":"So, what is the problem?"},{"paragraphs":[{"text":"In short, everyone says that the feasibility study is not credible. For example, the economic part of this document is poorly researched. It turned out that the analysis of the benefits and costs of the project <a href='/en/articles/economic-mistakes'>does not include  12 billion of investment costs</a>, the external costs are estimated incorrectly, and besides, the calculations are made only for two Polish sections of the route. <a href='/en/articles/negative-economics'> Another group of experts has noted</a> that the risks are poorly analyzed, the calculations for taking in cargoes from the railroads and automobile roads are not specified, and on the whole, the E40 waterway threatens to become another subsidized project."},{"text":"Some points are not taken into account in the feasibility study at all&mdash;for example, the fact that the E40 waterway and its construction works will be carried out in the areas contaminated by radiation as a consequence of the Chernobyl disaster. <a href='/en/articles/xrays'>Ukraine can be exposed to the most drastic consequences</a>. The E40 waterway will pass through more than 70 valuable natural areas of Poland, Belarus and Ukraine that will be suffer from irreparable damage. Theres almost no mention of the threats to the biodiversity of the whole region in the existing documents, therefore we asked several experts <a href='/en/articles/biodiversity'>what impact the construction of the E40 may have on the Pripyat river and Polesye.</a>"}],"title":"And what do independent experts say?"},{"paragraphs":[{"text":"Among large European rivers, the Pripyat is one of the least human-spoilt and the entire life of the Polesye region is indissolubly tied to it. Each year, 1.5 million birds gain strength in the floodplain of the Pripyat during migration, and more than 90% of the total number of birds in Belarus are recorded in Polesye. There are a lot of rare biotopes here, for example, unique floodplain oak groves and black alder forests; it is home to a large number of protected species of animals and plants, many of which are listed in the Red Book of the Republic of Belarus. Moreover, Polesye has a lot of swamps that absorb 20 times more CO2 than forests and can be severely damaged due to the change in the hydrological regime of the region. It should be noted that the E40 waterway will affect <a href='/en/articles/protected-areas'>some of the most valuable natural territories</a> in Belarus: Pripyat National Park, Polesye Radioecological Reserve, 11 republican sanctuaries and six local ones. Of these sites, 12 are of international importance."}],"title":"And what is the value of the Pripyat river and the Polesye region?"},{"paragraphs":[{"text":"First and foremost, we want to draw the attention of the Polish, Belarusian, Ukrainian governments and the European Parliament to this issue. We want to demonstrate that any decisions on the E40 waterway construction project should be made with the active involvement of the public and independent experts. We point to the fact that the E40 waterway project has a questionable economic value but definitely poses a serious threat to nature. We remind you: <a href='/en/articles/similar-stories'>the world practice has proved</a> that in the 21st century it is ships that should be adapted to rivers but not vice versa."}],"title":"So what do you want to achieve?"},{"paragraphs":[{"text":"You can sign the <a href='/en/pages/petition'>petition</a> that we will file to the governments of Poland, Belarus and Ukraine. If you represent any legal entity, you can join the <a href='/en/pages/position'> petition for organizations</a>."},{"text":"You can tell your family and friends in person about this issue. And, of course, you can \"like\", share and repost the story in social media&mdash;the more people know about this issue, the more chances that landmark decisions will be made with our participation and not on the sidelines."}],"title":"OK. You have persuaded me. How can I help you?"}],"slug":"about","teaser":"","title":"About"}

