module.exports = {"slug":"e40","teaser":"Officially: what is the essence of E40 waterway restoration project and what do the Belarusian government organizations think about it.","title":"E40","cover":"","date":"","id":6,"lead":"We have collected documents on the E40 waterway project which are available for public use, e.g. brochures, various feasibility studies and an environmental assessment of the project. Also here you can find 17 responses from various state institutions of Belarus to the official inquiries of the NGO \"Bahna\" about their position on the route arrangement project.","sections":[{"paragraphs":[{"list":[{"link":"https://drive.google.com/drive/folders/0B1Oup9NEQTK3YndKdGs4d2JONWs?usp=sharing","text":"Project brochure"},{"link":"https://drive.google.com/open?id=0B1Oup9NEQTK3Ym52c196M0lWMnM","text":"Expert advice"},{"link":"https://drive.google.com/open?id=0B1Oup9NEQTK3R2ZMdjctbzlQYjg","text":"Environmental assessment"},{"link":"https://drive.google.com/open?id=0B1Oup9NEQTK3WFU5eGF3RHRheU0","text":"05.2015, First Interim Report"},{"link":"https://drive.google.com/open?id=0B1Oup9NEQTK3Y3dyUkhFQVpDSk0","text":"06.2016, UN Minutes of an Environmental Council meeting"},{"link":"https://drive.google.com/open?id=0B1Oup9NEQTK3cFJBMTlJT0VhcU0","text":"08.2015, Second interim report"},{"link":"https://drive.google.com/open?id=0B1Oup9NEQTK3aXpOUzNacS04M0U","text":"30.06.2015, Comments byDNEPROBUG onthe 1st IntR_En"},{"link":"https://drive.google.com/open?id=0B1Oup9NEQTK3TXN0dmdUaGxhUVU","text":"Description of the project"}]}],"title":"Documents provided on the E40 waterway restoration project"},{"paragraphs":[{"text":"Govermental agencies were informed about the upcoming plans. The National Academy of Sciences, the Central Research and Institute for Complex Use of Water Resource, the Polesye State Radiation-Ecological Reserve and Pripyatsky National Park presented negative feedback and statements of concern."},{"list":[{"link":"https://drive.google.com/open?id=0BxEL2fyaXxP4Z1otdmNMeE5MLVE","text":"Written responses of government organizations"}]}],"title":"The position of government organizations on the project of main E40 waterway restoration"}]}

